import styled from "styled-components"

interface Props {
    clickFavoritos: boolean;
}


export const Container = styled.div.attrs((props: Props) => {
    return {
        clickFavoritos: props.clickFavoritos,
        style: {
            display: props.clickFavoritos ? 'block' : 'none'
        },
    };
})`
    width: 100%;
    min-height: 100vh;
    background: ${props => props.theme.corPadrao};

    position: fixed;
    top: 0%;
    bottom: 0%;
    left: 50%;
    transform: translateX(-50%);
    overflow-y: scroll;
    overflow-x: hidden;
    box-shadow: 2px 2px 5px #333;

    /* position: fixed; 
    bottom: 0%; 
    top: 0%; overflow: scroll; height: 100vh; */


    
    @media (min-width: 600px){
            width: 600px;
            margin: 0 auto;
        }


    .icone-close{
        cursor: pointer;
        position: absolute;
        right: 10px;
        top: 25px;
        font-size: 2rem;

        :hover{
           color:  red;
        }
    }

    h1{
        width: fit-content;
        margin: 0 auto;
        padding: 5px 20px;
        border-radius: 5px;
        text-align: center;
        margin-top: 20px;
        background: ${props => props.theme.corBranco};
        color: ${props => props.theme.corPadrao};
    }
`